import React from "react";
import MainSplideStyles from "./styles/MainSplideStyles";
// PACKAGES
import { Splide, SplideSlide } from "@splidejs/react-splide";
import "@splidejs/react-splide/css";

// ICONS
import { GrTextAlignFull } from "react-icons/gr";
import { IoVideocamOutline } from "react-icons/io5";
import { CgLink } from "react-icons/cg";
import { BsCamera } from "react-icons/bs";
import { BiMicrophone, BiStats } from "react-icons/bi";
import { VscGraphScatter } from "react-icons/vsc";
import moment from "moment";
import Translate from "../shared/Translate";
import { i18n } from "src/i18n";
import { Link } from "react-router-dom";

export default function MainSplide({ data }) {
  // HELPER FUNCTIONS
  const pipeDate = (date) => {
    let d = date.split("T")[0];
    return moment(d).format("DD-MM-YYYY");
  };
  const language = localStorage.getItem("language");
  return (
    <MainSplideStyles>
      <Splide
        options={{
          perPage: 1,
          perMove: 1,
          pagination: true,
          wheel: false,
          direction: language === "ar" ? "rtl" : "ltr",
        }}
        className="splide"
      >
        {data?.map((post, index) => {
          return (
            <SplideSlide
              key={index}
              className="horizontalPlayer"
              style={{
                background:
                  language === "fr"
                    ? "linear-gradient(115.19deg, #2B2840 28.27%, rgba(43, 40, 64, 0) 68.81%), linear-gradient(0deg, rgba(43, 40, 64, 0.7), rgba(43, 40, 64, 0.7)), url(" +
                      post.photos[0]?.downloadUrl +
                      ")"
                    : language === "ar"
                    ? "linear-gradient(115.19deg, #2B2840 28.27%, rgba(43, 40, 64, 0) 68.81%), linear-gradient(0deg, rgba(43, 40, 64, 0.7), rgba(43, 40, 64, 0.7)), url(" +
                      post.photosAR[0]?.downloadUrl +
                      ")"
                    : "linear-gradient(115.19deg, #2B2840 28.27%, rgba(43, 40, 64, 0) 68.81%), linear-gradient(0deg, rgba(43, 40, 64, 0.7), rgba(43, 40, 64, 0.7)), url(" +
                      post.photosEN[0]?.downloadUrl +
                      ")",
              }}
            >
              <Link to={"/Mediatheque/entity/" + post._id}>
                <section>
                  {language === "fr" ? (
                    <div>
                      {post.photos?.length ? (
                        <div
                          className="cardThumbnail"
                          style={{
                            backgroundImage:
                              "url(" + post.photos[0]?.downloadUrl + ")",
                          }}
                        ></div>
                      ) : (
                        <div className="cardThumbnail hasNoThumbnail"></div>
                      )}
                    </div>
                  ) : language === "ar" ? (
                    <div>
                      {post.photosAR?.length ? (
                        <div
                          className="cardThumbnail"
                          style={{
                            backgroundImage:
                              "url(" + post.photosAR[0]?.downloadUrl + ")",
                          }}
                        ></div>
                      ) : (
                        <div className="cardThumbnail hasNoThumbnail"></div>
                      )}
                    </div>
                  ) : (
                    <div>
                      {post.photosEN?.length ? (
                        <div
                          className="cardThumbnail"
                          style={{
                            backgroundImage:
                              "url(" + post.photosEN[0]?.downloadUrl + ")",
                          }}
                        ></div>
                      ) : (
                        <div className="cardThumbnail hasNoThumbnail"></div>
                      )}
                    </div>
                  )}

                  <div>
                    <div className="dateAndType">
                      <div
                        className="contentType"
                        style={{ width: "fit-content" }}
                      >
                        {
                          {
                            documentaire: (
                              <GrTextAlignFull
                                className="icon"
                                style={{ marginLeft: 5, marginRight: 5 }}
                              />
                            ),
                            podcast: (
                              <BiMicrophone
                                className="icon"
                                style={{ marginLeft: 5, marginRight: 5 }}
                              />
                            ),
                            videos: (
                              <IoVideocamOutline
                                className="icon"
                                style={{ marginLeft: 5, marginRight: 5 }}
                              />
                            ),
                            exposition: (
                              <VscGraphScatter
                                className="icon"
                                style={{ marginLeft: 5, marginRight: 5 }}
                              />
                            ),
                            reportage: (
                              <BsCamera
                                className="icon"
                                style={{ marginLeft: 5, marginRight: 5 }}
                              />
                            ),

                            autres: (
                              <BsCamera
                                className="icon"
                                style={{ marginLeft: 5, marginRight: 5 }}
                              />
                            ),
                          }[post.type]
                        }
                        {i18n(
                          `entities.mediatique.enumerators.type.${post.type}`
                        )}
                      </div>
                      <span className="contentDate">
                        {pipeDate(post.updatedAt)}
                      </span>
                    </div>

                    <div className="textContent">
                      <div className="horizontalPlayerTitle ow">
                        {Translate.Trans("title", post)}
                      </div>
                      <div
                        className="horizontalPlayerDescription text__wrap"
                        dangerouslySetInnerHTML={{
                          __html: Translate.Trans("description", post),
                        }}
                      ></div>
                    </div>
                  </div>
                </section>
              </Link>
            </SplideSlide>
          );
        })}
      </Splide>
    </MainSplideStyles>
  );
}
