import React from "react";
import styled from "styled-components";
import EntityPlayerButton from "../styles/EntityPlayerButton";
import moment from "moment";

// ICONS
import { IoVideocamOutline } from "react-icons/io5";
import Translate from "../../shared/Translate";
import { i18n } from "src/i18n";
import { Link } from "react-router-dom";
import { VscGraphScatter } from "react-icons/vsc";
import { BsCamera } from "react-icons/bs";
import { noImage } from "src/assets/images";

export default function VerticalPlayer({ dataSource }) {
  // HELPER FUNCTIONS
  const pipeDate = (date) => {
    let d = date.split("T")[0];
    return moment(d).format("DD-MM-YYYY");
  };
  const langue = localStorage.getItem("language");
  const Substring = (text, limit) => {
    if (text) {
      if (text.length > limit) {
        return text.substring(0, limit) + "...";
      } else return text;
    } else return text;
  };
  return (
    <VerticalPlayerCard key={dataSource._id} className="videoCardVertical">
      <Link to={"/Mediatheque/entity/" + dataSource._id}>
        <>
          {langue === "fr" ? (
            <>
              {" "}
              {dataSource.photos?.length > 0 ? (
                <div
                  className="cardThumbnail"
                  style={{
                    backgroundImage:
                      "url(" + dataSource.photos[0].downloadUrl + ")",
                  }}
                ></div>
              ) : (
                <div className="cardThumbnail hasNoThumbnail"></div>
              )}
            </>
          ) : langue === "ar" ? (
            <>
              {" "}
              {dataSource.photosAR?.length > 0 ? (
                <div
                  className="cardThumbnail"
                  style={{
                    backgroundImage:
                      "url(" + dataSource.photosAR[0].downloadUrl + ")",
                  }}
                ></div>
              ) : (
                <div className="cardThumbnail hasNoThumbnail"></div>
              )}
            </>
          ) : (
            <>
              {" "}
              {dataSource.photosEN?.length > 0 ? (
                <div
                  className="cardThumbnail"
                  style={{
                    backgroundImage:
                      "url(" + dataSource.photosEN[0].downloadUrl + ")",
                  }}
                ></div>
              ) : (
                <div className="cardThumbnail hasNoThumbnail"></div>
              )}
            </>
          )}
        </>

        <div className="dateAndType">
          {dataSource.type === "exposition" ? (
            <div className="contentType" style={{ width: "inherit" }}>
              <VscGraphScatter className="icon" />

              {Substring(
                i18n(`entities.mediatique.enumerators.type.${dataSource.type}`),
                20
              )}
            </div>
          ) : dataSource.type === "reportage" ? (
            <div className="contentType" style={{ width: "inherit" }}>
              <BsCamera className="icon" />
              {Substring(
                i18n(`entities.mediatique.enumerators.type.${dataSource.type}`),
                20
              )}
            </div>
          ) : dataSource.type === "autres" ? (
            <div className="contentType" style={{ width: "inherit" }}>
              <BsCamera className="icon" />
              {Substring(
                i18n(`entities.mediatique.enumerators.type.${dataSource.type}`),
                20
              )}
            </div>
          ) : (
            <div className="contentType" style={{ width: "inherit" }}>
              <IoVideocamOutline className="icon" />
              {Substring(
                i18n(`entities.mediatique.enumerators.type.${dataSource.type}`),
                20
              )}
            </div>
          )}
          <span className="contentDate">{pipeDate(dataSource.updatedAt)}</span>
        </div>
        {/* <div className="verticalPlayerTitle text__wrap">
          <span>{Translate.Trans("title", dataSource)}</span>
        </div> */}
      </Link>
    </VerticalPlayerCard>
  );
}

const VerticalPlayerCard = styled.div`
  position: relative;
  height: inherit !important;
  overflow: hidden;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  .cardThumbnail {
    width: 100%;
    height: 200px;
    background-size: cover;
    background-position: center;
    position: relative;
  }
  .hasNoThumbnail {
    background-image: url(${noImage});
  }

  .dateAndType {
    margin-top: 8px;
    height: 30px;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;

    span {
      color: #fff;
    }

    .contentType {
      width: 100px;
      height: 30px;
      border: 1px solid #e1011a;
      border-radius: 0.5rem;

      font-family: "Proxima Nova";
      font-style: normal;
      font-weight: 400;
      font-size: 15px;
      line-height: 15px;
      color: red;
      padding: 0.3rem 0.5rem;
      display: flex;
      align-items: center;
      justify-content: space-evenly;

      .icon {
        margin-right: 5px;
      }
    }

    .contentDate {
      color: rgba(122, 122, 122, 1);
      font-size: 12px;

      font-family: "Proxima Nova";

      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 15px;
    }
  }

  .verticalPlayerTitle {
    margin-top: 1rem;
    height: 70px;

    span {
      width: 100%;
      font-family: "Proxima Nova";
      text-transform: capitalize !important;
      line-height: 1.5;
      font-weight: bold;
      font-size: 16px;
      color: var(--violet);

      overflow: hidden;
      display: inline-block;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
`;
